@use '_variables' as *;

.down-container{
    font-family: "TT Lakes";
    padding-left:16px;
    padding-top:32px;
    padding-bottom:50px;
    display: flex;
    flex-direction: column;

    &__text{
        font-weight:400;
        font-size:14px;
        line-height: 20px;
        letter-spacing:0.2px;
        color:$text-color;
        display: flex;
        justify-content: center;
        align-items: center;
    }



    &__button{
        font-family: "TT Lakes";
        display: flex;
        gap:10px;
        min-width:192px;
        height: auto;
        border:transparent;
        background-color:$main-color;
        font-weight: 500;
        font-size: 16px;
        line-height:24px;
        letter-spacing:-0.2px;
        .down-container__button--arrow{
            color:$buttonArrowColor;
        }
    }
}

.down-container__button:hover{
    opacity:0.7;
}
