@use 'content-text-style';
@use 'variables' as *;
@use 'button' as *;
@use 'fonts' as *;
@use 'services' as *;
@use 'swipers-style';
@use 'down-container';
@use 'footer';
@use 'modal';
@use 'grid';

.add-buttons {
  display: none;
}

.button__section--add {
  display: none;
}

.content__text--second,
.content__text--tridth {
  display: none;
}

@media (min-width: 768px) and (max-width: 1336px) {
  .add-buttons {
    display: flex;
    gap:16px;
  }

  .line--add {
    display: flex;
    width: 2px;
    height: 32px;
    background-color: $line-color;
    margin-left: 20px;
    margin-right: 20px;
  }

  .line {
    display: flex;
    justify-content: flex-end;
  }

  .content {
    display: flex;
    gap: 48px;

    &__wrapper {
      padding-top: 32px;
      padding-left: 24px;
    }

    .content__text--second {
      display: block;
    }

    .content__text--tridth {
      display: none;
    }

    &__image {
      max-width: 50%;
      height: 208px;
      padding-right: 24px;
      padding-top: 20px;
    }
  }
  .right--button{
    display: flex;
    justify-content: flex-end;
  }
  .down-container {
    font-family: 'TT Lakes';
    max-width: 421px;
    padding-left: 32px;
    padding-bottom: 50px;
    display: flex;
    flex-direction: column;

    &__text {
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      letter-spacing: 0.2px;
      color: $text-color;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    &__button {
      font-family: 'TT Lakes';
      display: flex;
      min-width: 192px;
      height: auto;
      border: transparent;
      background-color: $main-color;
      font-weight: 500;
      font-size: 16px;
      line-height: 24px;
      letter-spacing: -0.2px;
    }
    .down-container__button--arrow{
            color:$buttonArrowColor;
        }
  }

  .swiper {
    display: none;
  }

  .main__footer {
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  padding: 40px 24px 19px 24px;
  display: flex;
  justify-content:space-between;
  align-items: center;
  flex-direction: row;
  color: $swiper-title-color;
  opacity: 72.18%;
  

  
  .copyright {
    display: flex;
    justify-content: center;
  }
  &__privacy-policy {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__disclaimer {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 28px;
  }
}
.content__button--watch{
  padding-top:16px;
}
 .modal.visable{
  display: block;
 }
}
@media (min-width: 1336px) {

  .content__button--watch{
  padding-top:16px;
  }

  header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  content {
    display: flex;
    gap: 48px;
  }

  .content__wrapper {
    padding-top: 32px;
    padding-left: 24px;
  }

  .content__text--second,
  .content__text--tridth {
    display: block;
  }

  .button__section--add {
    padding-right: 30px;
  }

  .navigation__button--icon {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 8px;
    background-color: $main-color;
  }

  .grid {
    display: block;
  }

  .button__section--add {
    font-family: "TT Lakes";
    font-weight:500;
    font-size:16px;
    line-height:24px;
    letter-spacing:-0,2;
    text-align: right;
    display: flex;
    gap:32px;
  }

  .grid__title {
    display: flex;
    align-items: center;
    height: 80px;
    padding-left: 16px;
    border-top: 1px solid $swiper-title-border;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    text-transform: uppercase;
    color: $button-color;
    background: $gradient;
  }

  .brands__wrapper {
    display: grid;
    grid-template-columns: repeat(auto-fill, 240px);
    gap: 16px;
    padding-top: 16px;
    padding-left: 16px;
    padding-right: 24px;
    max-height: 180px;
    overflow: hidden;
    transition: max-height 0.5s ease;
  }

  .brands__wrapper.show-brands {
    max-height: 1000px;
  }

  .logo-section {
    display: flex;
    width: 60%;
  }
  .brands__icon {
    display: flex;
    align-items: center;
    width: 224px;
    height: 72px;
    background-color: white;
    padding-left: 16px;
    border: 1px solid $line-color;
    border-radius: 6px;
  }
  .brands__icon--arrow {
    padding-left: 16px;
  }

  .content__button--watch {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $main-color;
    font-family: 'TT Lakes';
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    color: $text-color;
  }

  .devices__wrapper {
    display: grid;
    grid-template-columns: repeat(auto-fill, 240px);
    padding: 24px;
    gap: 16px;
    max-height: 190px;
    overflow: hidden;
    transition: max-height 0.5s ease;
  }

  .devices__wrapper.show-devices {
    max-height: 1000px;
  }

  .devices {
    width: 224px;
    height: 160px;
    background-color: white;
    border: 1px solid $line-color;
    border-radius: 6px;

    &__icon--arrow {
      width: 40px;
      height: 40px;
      position: relative;
      top: 104px;
      left: 9px;
    }

    &__text {
      font-weight: 500;
      font-size: 16px;
      line-height: 24px;
      letter-spacing: -0.2px;
      padding-left: 16px;
      width: 100%;
    }
  }

  .button--order {
    width: 119px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #ff3e79;
    border-radius: 16px;
    gap: 8px;
    color: #ffffff;
    padding: 16px 16px 16px 20px;
  }

  .brands__button,
  .devices__button {
    background-color: $main-color;
    display: flex;
    align-items: center;
    padding-bottom: 32px;
    padding-top: 24px;
  }

  table {
    padding-left: 16px;
    padding-right: 24px;
    display: block;
    width: 100%;
    border-collapse: collapse;
  }

  td,
  th {
    padding: 16px;
    width: 100vh;
    padding-left: 24px;
  }

  td {
    background-color: white;
    margin-left: 24px;
  }

  tbody tr {
    position: relative;
  }

  tbody tr::after {
    content: '';
    position: absolute;
    left: 20px;
    right: 20px;
    bottom: 0;
    height: 1px;
    background-color: $main-color;
  }

  tbody tr:last-child::after {
    display: none;
  }

  button:hover,
  button:focus {
    opacity: 0.6;
  }

  .down-container {
    font-family: 'TT Lakes';
    max-width: 421px;
    padding-left: 32px;
    padding-bottom: 50px;
    display: flex;
    flex-direction: column;

    &__text {
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      letter-spacing: 0.2px;
      color: $text-color;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    &__button {
      font-family: 'TT Lakes';
      display: flex;
      min-width: 192px;
      height: auto;
      border: transparent;
      background-color: $main-color;
      font-weight: 500;
      font-size: 16px;
      line-height: 24px;
      letter-spacing: -0.2px;
    }
  }

  .content {
    display: flex;
    justify-content: space-between;
    align-items: center;

    &__wrapper {
      padding-left: 24px;
    }

    &__image {
      max-width: 50%;
      height: 208px;
      padding-right: 24px;
    }
  }

  .modal {
    display: block;
    .modal__content {
      display: flex;
      align-items: center;
      flex-direction: column;
      position: fixed;
      top: 0;
      left: 0;
      z-index: 200;
      width: 276px;
      height: 100%;
      box-shadow: none;
      background-color: #ffffff;
      overflow: auto;
      padding-top: 18px;
    }

    &__backdrop {
      background-color: transparent;
    }
  }

  .main__header {
    display: flex;
    justify-content: space-between;
  }

  main {
    padding-left: 276px;
  }

  .swiper {
    display: none;
  }

  .main__footer {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    padding: 83px 33px 21px 31px;
    gap: 100px;
  }

  .modal__content__headar {
    justify-content: space-around;
    .headar__button--close {
      display: none;
    }
  }
  .content__wrapper   {
  display: flex;
    flex-direction: column;
    justify-content: center;
    padding-bottom:198px;
  }

  .main__footer {
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  padding: 40px 24px 19px 24px;
  display: flex;
  justify-content:space-between;
  align-items: center;
  flex-direction: row;
  color: $swiper-title-color;
  opacity: 72.18%;
  

  
  .copyright {
    display: flex;
    justify-content: center;
  }
  &__privacy-policy {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__disclaimer {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 28px;
  }
}
}